/*
  Responsive Breakpoints
  Inspired by https://medium.com/developing-with-sass/creating-a-dead-simple-sass-mixin-to-handle-responsive-breakpoints-889927b37740
*/
header {
  background: #666 no-repeat 50% 65%;
  background-image: url("/static/background-header.jpg");
  background-size: cover;
  width: 100%;
  min-height: 100vh;
  color: #fff;
  text-align: center;
}
@media (max-height: 480px) and (max-width: 1024px) {
header {
      height: 650px;
      position: relative;
}
}
header .overlay-top, header .overlay-bottom {
    background: -webkit-gradient(linear, left top, left bottom, from(black), to(transparent));
    background: linear-gradient(black, transparent);
    width: 100%;
    position: absolute;
    opacity: 0.7;
    z-index: 5;
}
header .overlay-top {
    height: 250px;
}
header .overlay-bottom {
    height: 350px;
    bottom: 0;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
header .container {
    position: relative;
    z-index: 10;
    height: 100vh;
}
@media (max-height: 480px) and (max-width: 1024px) {
header .container {
        height: 100%;
}
}
header .top, header .bottom {
    position: absolute;
    width: 100%;
}
header .top {
    top: 0;
}
header .top .title {
      display: inline-block;
      padding: 8px 28px;
      border: 6px solid #fff;
      font-family: "Montserrat", sans-serif;
      font-style: italic;
      font-weight: 700;
      font-size: 42px;
      margin-top: 70px;
      margin-bottom: 450px;
}
@media (max-width: 479px) {
header .top .title {
          font-size: 22px;
          margin-top: 30px;
          margin-bottom: 210px;
          border: 4px solid #fff;
}
}
@media (min-width: 480px) and (max-width: 767px) {
header .top .title {
          font-size: 28px;
          margin-top: 40px;
          margin-bottom: 400px;
}
}
@media (min-width: 768px) and (max-width: 1024px) {
header .top .title {
          font-size: 32px;
          margin-top: 50px;
          margin-bottom: 400px;
}
}
header .bottom {
    bottom: 0;
}
header .bottom .name {
      margin-top: 50px;
      margin-bottom: 17px;
}
header .bottom .name h1 {
        font-family: "Josefin Sans", sans-serif;
        font-weight: 700;
        font-size: 72px;
}
@media (max-width: 479px) {
header .bottom .name {
          margin-bottom: 8px;
}
header .bottom .name h1 {
            font-size: 42px;
}
}
@media (min-width: 480px) and (max-width: 767px) {
header .bottom .name {
          margin-bottom: 10px;
}
header .bottom .name h1 {
            font-size: 56px;
}
}
@media (min-width: 768px) and (max-width: 1024px) {
header .bottom .name {
          margin-bottom: 15px;
}
header .bottom .name h1 {
            font-size: 60px;
}
}
header .bottom .job-position {
      margin-bottom: 40px;
}
header .bottom .job-position h2 {
        font-family: "Montserrat", sans-serif;
        font-weight: 500;
        font-size: 22px;
        text-transform: uppercase;
        letter-spacing: 5px;
}
@media (max-width: 479px) {
header .bottom .job-position {
          margin-bottom: 15px;
}
header .bottom .job-position h2 {
            font-size: 14px;
}
}
@media (min-width: 480px) and (max-width: 767px) {
header .bottom .job-position {
          margin-bottom: 10px;
}
header .bottom .job-position h2 {
            font-size: 16px;
}
}
@media (min-width: 768px) and (max-width: 1024px) {
header .bottom .job-position {
          margin-bottom: 40px;
}
header .bottom .job-position h2 {
            font-size: 18px;
}
}
header .bottom .contact {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin-bottom: 50px;
}
@media (max-width: 479px) {
header .bottom .contact {
          margin-bottom: 25px;
}
}
header .bottom .contact-grid {
        display: grid;
        grid-template-columns: 300px 300px;
        margin: 0 auto;
}
@media (max-width: 767px) {
header .bottom .contact-grid {
            grid-template-columns: 200px;
            grid-row-gap: 10px;
}
}
header .bottom .contact-grid-item {
          margin: 0 auto;
          padding: 10px 20px;
}
header .bottom .contact-grid-item-icon {
            font-size: 42px;
}
header .bottom .contact-grid-item-info {
            font-family: "Quicksand", sans-serif;
            font-weight: 500;
}
@media (max-width: 767px) {
header .bottom .contact-grid-item {
              padding: 0 20px;
}
header .bottom .contact-grid-item-info {
                font-weight: 400;
}
}
section {
  padding-top: 30px;
  text-align: center;
}
section .title {
    font-family: "Montserrat", sans-serif;
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 18px;
}
@media (max-width: 479px) {
section .title {
        font-size: 18px;
}
}
section .content {
    font-family: "Quicksand", sans-serif;
    font-size: 16px;
}
@media (max-width: 767px) {
section .content {
        font-size: 14px;
}
}
section .content.grid-container {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
section .content-item-birth, section .content-item-address {
      margin-bottom: 8px;
}
section .content-grid {
      display: grid;
      margin: 0 auto;
}
section .content-grid.education, section .content-grid.organization, section .content-grid.work {
        grid-template-columns: 500px 500px;
}
@media (max-width: 1023px) {
section .content-grid.education, section .content-grid.organization, section .content-grid.work {
            grid-template-columns: 100%;
            grid-row-gap: 10px;
}
}
@media (min-width: 1024px) and (max-width: 1200px) {
section .content-grid.education, section .content-grid.organization, section .content-grid.work {
            grid-template-columns: 400px 400px;
}
}
section .content-grid.education .content-grid-item:hover .content-grid-item-icon, section .content-grid.organization .content-grid-item:hover .content-grid-item-icon, section .content-grid.work .content-grid-item:hover .content-grid-item-icon {
          -webkit-transform: rotate(30deg);
                  transform: rotate(30deg);
          color: #C70039;
}
section .content-grid.skill {
        grid-template-columns: 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px 90px;
}
@media (max-width: 767px) {
section .content-grid.skill {
            grid-template-columns: 100px 100px 100px;
}
}
@media (min-width: 768px) and (max-width: 1023px) {
section .content-grid.skill {
            grid-template-columns: 100px 100px 100px 100px 100px 100px;
}
}
@media (min-width: 1024px) and (max-width: 1200px) {
section .content-grid.skill {
            grid-template-columns: 100px 100px 100px 100px 100px 100px 100px;
}
}
@media (max-width: 1024px) {
section .content-grid.skill {
            grid-row-gap: 10px;
}
}
section .content-grid.skill .content-grid-item:nth-child(3) img, section .content-grid.skill .content-grid-item:nth-child(7) img, section .content-grid.skill .content-grid-item:nth-child(11) img {
          width: 45px;
}
section .content-grid.skill .content-grid-item:nth-child(8) img {
          width: 60px;
}
section .content-grid.skill .content-grid-item:hover .content-grid-item-logo img {
          -webkit-transform: scale(1.4, 1.4);
                  transform: scale(1.4, 1.4);
}
section .content-grid.skill .content-grid-item:hover .content-grid-item-skill-name {
          font-weight: 500;
}
section .content-grid.skill .content-grid-item:hover .content-grid-item-rate {
          color: #EFCE4A;
}
section .content-grid.interest {
        grid-template-columns: 250px 250px 250px;
}
@media (max-width: 767px) {
section .content-grid.interest {
            grid-template-columns: 100%;
}
}
@media (min-width: 768px) and (max-width: 1024px) {
section .content-grid.interest {
            grid-template-columns: 220px 220px 220px;
}
}
@media (max-width: 1024px) {
section .content-grid.interest {
            grid-row-gap: 10px;
}
}
section .content-grid.portfolio {
        grid-template-columns: 160px 160px;
}
section .content-grid.portfolio .content-grid-item:nth-child(2) img {
          width: 40px;
}
section .content-grid.social-media {
        grid-template-columns: 160px 160px 160px 160px 160px 160px;
}
@media (min-width: 480px) and (max-width: 767px) {
section .content-grid.social-media {
            grid-template-columns: 160px 160px;
}
}
@media (min-width: 768px) and (max-width: 1024px) {
section .content-grid.social-media {
            grid-template-columns: 160px 160px 160px;
}
}
@media (max-width: 1024px) {
section .content-grid.social-media {
            grid-row-gap: 20px;
}
}
section .content-grid.portfolio .content-grid-item-logo img, section .content-grid.social-media .content-grid-item-logo img {
        height: 38px;
}
@media (max-width: 479px) {
section .content-grid.portfolio, section .content-grid.social-media {
          grid-template-columns: 140px 140px;
}
}
section .content-grid-item a {
        color: #000000;
        text-decoration: none;
}
section .content-grid-item a:hover .content-grid-item-username {
        font-weight: 500;
}
section .content-grid-item a:hover .content-grid-item-logo img {
        -webkit-transform: scale(1.3, 1.3);
                transform: scale(1.3, 1.3);
}
section .content-grid-item-icon {
        font-size: 28px;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
}
section .content-grid-item-logo {
        height: 60px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
section .content-grid-item-logo img {
          -webkit-transition: -webkit-transform 0.3s ease;
          transition: -webkit-transform 0.3s ease;
          transition: transform 0.3s ease;
          transition: transform 0.3s ease, -webkit-transform 0.3s ease;
          margin: auto;
          height: 32px;
}
section .content-grid-item-image {
        height: 135px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
section .content-grid-item-image object {
          height: 100px;
          margin: auto;
}
section .content-grid-item-main-content {
        font-size: 18px;
}
@media (max-width: 479px) {
section .content-grid-item-main-content {
            font-size: 16px;
}
}
section .content-grid-item-main-content, section .content-grid-item-sub-content {
        font-weight: 500;
}
section .content-grid-item-year, section .content-grid-item-main-content, section .content-grid-item-sub-content {
        margin-bottom: 6px;
}
section .content-grid-item-skill-name {
        margin-bottom: 2px;
}
section .content-grid-item-username {
        -webkit-transition: all 0.1s ease;
        transition: all 0.1s ease;
}
section .content-grid-item-rate {
        color: #B3B6B7;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
}
section .content-grid-item-rate span {
          margin: 0 1px;
}
section hr {
    margin: 0 auto;
    margin-top: 35px;
    width: 50%;
    border-top: 2px dashed #000000;
}
footer {
  text-align: center;
  margin: 25px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
}
* {
  padding: 0;
  margin: 0;
}
html, body {
  width: 100%;
  height: 100vh;
}
body {
  font-size: 16px;
}
@media (max-width: 480px) {
body {
      font-size: 14px;
}
}
.container {
  width: 90%;
  max-width: 1170px;
  margin: 0 auto;
}
